.buttonsContainer {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
    gap: 10px;
}

.clock {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
    border: 2px solid #ccc;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.watch {
    font-size: 20px;
}

.watch strong {
    font-size: 24px;
}

.more {
    margin-top: 10px;
    font-size: 16px;
}

.more span {
    margin-right: 5px;
}
