.button {
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    outline: none;
    transition: background-color 0.3s;
}

.disabled {
    cursor: not-allowed;
    opacity: 0.5;
}

.secondary {
    /*background-color: #4CAF50;*/
    /*color: white;*/
    color: black;
}

.default {
    background-color: #2196F3;
    color: white;
}

.red {
    background-color: rgba(238, 72, 72, 0.78);
    color: white;
}

.default:hover {
    background-color: #0b7dda;
}

.red:hover {
    background-color: rgba(205, 48, 48, 0.78);
}

.secondary:hover {
    background-color: #ababab;
    /*background-color: #45a049;*/
}

.default:active {
    background-color: #0a5c99;

}

.secondary:active {
    background-color: #888888;
    /*background-color: #3e8e41;*/
}

.red:active {
    background-color: rgba(171, 41, 41, 0.78);
}

.button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
}
