.input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 2px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease;
}

.input:focus {
    border-color: #4CAF50;
    outline: none;
}

.inputWrapper {
}

.superInput {
}

.errorInput {
    border-color: #FF5722;
}

.errorInput:focus {
    border: #cc0000;
}

.error {
    color: #FF5722;
    font-size: 14px;
    margin-top: 5px;
    transition: opacity 0.3s ease;
}
