/* SuperEditableSpan.module.css */

.spanBlock {
    display: flex;
    align-items: center;
}

.span {
    display: inline-block;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-left: 8px;
    cursor: pointer;
}

.input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-left: 8px;
    font-size: 14px;
}

.pen {
    width: 16px;
    height: 16px;
    margin-right: 4px;
}
