/* Sidebar.module.css */

.sidebar {
    width: 137px; /* Set the width of the sidebar */
    height: 100vh;
    position: fixed;
    top: 0;
    left: -240px; /* Hide the sidebar by default */
    background-color: #ffffff; /* Sidebar background color */
    z-index: 10;
    /*transition: left 0.3s ease;*/
}

.sidebar.open {
    left: 0; /* Display the sidebar when 'open' class is applied */
    width: 317px;

}

.background {
    background-color: rgba(0, 0, 0, 0.3); /* Semi-transparent background for the main content when the sidebar is open */
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0; /* Ensure the background covers everything */
}

.nav {
    padding: 20px; /* Add padding to the navigation links */
}

.nav a {
    display: block;
    padding: 10px 0;
    text-decoration: none;
    color: #1e4263; /* Link text color */
    font-size: 18px; /* Link text size */
    transition: color 0.3s ease; /* Smooth color transition */
}

.nav a:hover {
    color: #639dd1;
    text-decoration: underline;
}

.nav a.active {
    font-weight: bold; /* Bold font for the active link */
    color: #3d85c6; /* Active link text color */
}

.close {
    background: none;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;
}

.close img {
    width: 20px; /* Set the width of the close icon */
}
