/* GreetingForm Styles */
.greetingForm {
    text-align: center;
    margin: 20px;
}

/* Input and Button Container Styles */
.inputAndButtonContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

/* Error Message Styles */
.error {
    color: red;
    margin-top: 10px;
}

/* Input Styles */
.input {
    padding: 8px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 20px;
}

.input:focus {
    outline: none;
    border-color: dodgerblue;
    /*box-shadow: 0 0 5px rgba(0, 0, 255, 0.5);*/
}

/* Error Input Styles */
.errorInput {
    border-color: red;
}

/* Button Styles */
.button {
    padding: 10px 20px;
    font-size: 16px;
    background-color: dodgerblue;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.button:focus {
    outline: none;
}

.button:active {
    background-color: royalblue;
}

.button:disabled {
    background-color: lightgray;
    cursor: not-allowed;
}

/* Text Styles */
.text {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
}

/* Greeting Message Styles */
.greeting {
    font-size: 18px;
    margin-top: 10px;
}
